.table_container{
    display: flex;                    /* 1 */
    align-items: center;
    justify-content: center;
}

tr{
    display: flex;
    direction:row;
}
tr>td>input{
    width:97%
}
.hi{
    border-radius:0;
    border-style: line;
    border: transparent;
    background-color: transparent;
    text-align: center;

}
table{
    width: 100%;
    left: 0;
    top: 0;
    table-layout: fixed;
    border-collapse: collapse;
    border: 1px solid #5b5c5e;
    background-color:  #E5e1e6;
    margin-bottom: 3%;
  }

tr.heading{
    height: 5%;
}

tr.heading>td{
    font-size: 12px;
    text-align: center;
    width: 50%;
    text-align: center;
}

table.prevalence{
    margin-right: 1em;
}

tr>td:first-child{
    border-right: 1px solid #5b5c5e;
}

tr>td{
    border-bottom: 1px solid #5b5c5e;
}

table>tr:last-child>td{
    border-bottom: transparent;
}


