
.grid{
    display: flex;
    flex-direction: column;
    justify-content: left;
    background-color:#D7D2CB;
    padding:1%;
    height: 350px;
    min-width: 300px;
    max-width: 350px;
    border-width: 2px;
    border-color: #75787B;
    border-radius: 3px;
    border-style: solid;
    margin-top: 2%;
    margin-right: 7%;
    margin-left: 2%;
    font-size: 10px;
    margin-bottom: 4px;
}

.param-button{
    display: flex;
    text-align: left;
    font-family: Arial, sans-serif;
    background-color:#D7D2CB;
    max-height: 50%;
    width: 100%;
    color: #861F41;
    border-style: none;
    padding-top: 1%;
  }

.estimate_btn1{
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: Arial, sans-serif;
    background-color:#861F41;
    text-align: center;
    height: 35px;
    width: 100px;
    color: #FFFFFF;
    border-radius: 4px;
    border-color: #75787B;
    margin-top: 15px;
}
svg{
    padding-left: 7px;
    padding-right: 0px;
    float: right;
}
.text_boxes{
    text-align: left;
    color: #861F41;
    font-size: 16px;
    background-color: rgb(211,211,211, 0);
    padding-top: 1%;
    padding-bottom: 5%;
}

.ParamBody2 {
    margin-top:10px;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: row;
    align-items: top;
    justify-content: left;
    font-size: calc(10px + 2vmin);
    color: #861F41;
    overflow: hidden;
    font-family: Arial, sans-serif;
    padding-bottom: 600px;
  }

  .column>button{
    float: left;
    width: 40%;
    text-align: center;
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 5%;
    color: #75787B
  }
  
  div.row{
    display: flex;
    margin-bottom: 3%;
  }
  

